body {
  font: 14px 'Helvetica Neue', Helvetica, Arial, sans-serif;
  line-height: 1.4em;
  background: #f5f5f5;
  color: #4d4d4d;
  min-width: 230px;
  max-width: 100%;
  margin: 0 auto;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-weight: 300;
}


.load-button {
  display:block;
  margin: 300px auto;
  width: 130px;
  height: 50px;
  background-color: white;
  border: 1px solid #a3a3a3;
  border-radius: 5px;
  box-shadow: 0 1px 5px #a3a3a3;
  font-size: 20px;
}

.load-button:hover {
  cursor: pointer;
  width: 140px;
  height: 60px;
  position: relative;
  font-size: 23px;
}

.delete {
  width: 20px;
  height: 20px;
  background-color: white;
  border: 1px solid #a3a3a3;
  border-radius: 5px;
  box-shadow: 0 1px 5px #a3a3a3;
  font-size: 10px;
}

.delete:hover {
  cursor: pointer;
  position: relative;
  font-size: 10px;
  box-shadow: 0 1px 5px #7bcee7;
}

.progress {
  color: brown;
  opacity: 0.8;
}

.done {
  color: rgb(110, 197, 102);
  opacity: 0.8;
}

#root {
  height: auto;
  display: flex;
  /* flex-direction: column; */
  justify-content: space-around;
  align-items: center;
  margin: 20px;
}

.App {
  text-align: center;
  display: grid;
  grid-template-columns: 400px;
  justify-content: center;
}

.todoitem {
  display: flex;
  width: 100%;
  box-shadow: 0 1px 5px #a3a3a3;
}

select {
  margin: 15px;
  box-shadow: 0 1px 5px #a3a3a3;
  height: 30px;
}

.todoitem_title {
 width: 400px;
}

.status {
  width: 200px;
}

.delete {
  list-style-type: none;
  width: 40px;
  padding: 0;
  opacity: 0.8;
}

.user_user-name {
width: 200px;
}

